<template>
    <div class="container">
      <h1>Latest Posts</h1>
      <!-- CREATE POST HERE -->
      <div class="create-post">
        <label for="create-post">Say Something...</label>
        <input type="text" id="create-post" v-model="text" placeholder="Create A New Post" @keyup="createPostOnEnter">
        <button @click="createPost">Post</button>
      </div>
      <hr>
      <p class="error" v-if="error">{{error}}</p>
      <div class="posts-container">
        <div class="post"
        v-for="(post, i) in posts"
        :item="post"
        :index="i"
        :key="post._id"
        @dblclick="deletePost(post._id)">
        {{`${monthNames[post.createdAt.getMonth()]} ${post.createdAt.getDate()}, ${post.createdAt.getFullYear()}`}}
        <p class="text">{{post.text}}</p>
        </div>
      </div>
    </div>
</template>

<script>
import PostsService from '../PostsService'
export default {
  name: 'Post Component',
  data() {
    return {
      posts: [],
      error: '',
      text: '',
      monthNames: ["January", "February", "March", "April", "May", "June",
  "July", "August", "September", "October", "November", "December"]
    }
  },
  async created() {
    try {
      this.posts = await PostsService.getPosts()
    } catch(err) {
      this.error = err.message;
    }
  },
  methods: {
    async createPostOnEnter(e) {
      if(e.keyCode == '13'){
        await PostsService.insertPost(this.text)
        this.text = ''
        this.posts = await PostsService.getPosts()
      }
    },
    async createPost() {
      await PostsService.insertPost(this.text)
      this.text = ''
      this.posts = await PostsService.getPosts()
    },
    async deletePost(id) {
      await PostsService.deletePost(id)
      this.posts = await PostsService.getPosts()
    }
  },
  
}
</script>

