  }

  div.post {
    position:relative;
    border: 1px solid #5bd658;
    background-color: #bcffb8;
    padding: 10px 10px 30px 10px;
    margin-bottom: 15px;
  }

  div.created-at {
    position:absolute;
    top:0;
    left:0;
    padding: 5px 15px 5px 15px;
    background: darkgreen;
    color:white;
    font-size:13px;
  }

  p.text {
    font-size:22px;
    font-weight:700;
    margin-bottom:0;
  }

</style>
